import React, { useState, useEffect, useContext } from 'react';
import { store } from '../../store';
import { Layout } from '../Layout';
import {
  ProjectsContainer,
  ProjectContainer,
  ProjectTextContainer,
  Image,
  Title,
  Body,
  LoadingSpinner,
  Spinner
} from './styles/styles';

const PROJECTS_API_ENDPOINT = 'http://localhost:8080/projects';

export const Work = () => {
  const globalState = useContext(store);
  const {
    dispatch,
    state: { projects }
  } = globalState;

  useEffect(() => {
    const getProjectsRequest = async () => {
      const params = {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json'
        }
      };
      const projectsList = await fetch(PROJECTS_API_ENDPOINT, params)
        .then((res) => res.json())
        .catch((error) => console.log(error.message));

      console.log(projectsList);
      dispatch({ type: 'setProjects', payload: { projects: projectsList } });
    };

    if (!projects) {
      getProjectsRequest();
    }
  }, [dispatch, projects]);

  return (
    <Layout>
      {!projects ? (
        <LoadingSpinner>
          <Spinner />
        </LoadingSpinner>
      ) : (
        <ProjectsContainer>
          {projects &&
            projects.map((project) => (
              <ProjectContainer key={project.id}>
                <Image src={project.image} />
                <ProjectTextContainer>
                  <Title>{project.title}</Title>
                  <Body>{project.description}</Body>
                </ProjectTextContainer>
              </ProjectContainer>
            ))}
        </ProjectsContainer>
      )}
    </Layout>
  );
};
