import React, { useContext } from 'react';
import { NavbarContainer, LinkContainer, Link } from './styles/styles';
import { store } from '../../store';

export const Navbar = () => {
  const globalState = useContext(store);
  const { dispatch } = globalState;

  return (
    <NavbarContainer>
      <LinkContainer>
        <Link
          onClick={() =>
            dispatch({ type: 'setLocation', payload: { location: 0 } })
          }
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 1 }}
        >
          home
        </Link>
        <Link
          onClick={() =>
            dispatch({ type: 'setLocation', payload: { location: 1 } })
          }
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 1 }}
        >
          about
        </Link>
        <Link
          onClick={() =>
            dispatch({ type: 'setLocation', payload: { location: 2 } })
          }
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 1 }}
        >
          work
        </Link>
        <Link
          onClick={() =>
            dispatch({ type: 'setLocation', payload: { location: 3 } })
          }
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 1 }}
        >
          contact
        </Link>
      </LinkContainer>
    </NavbarContainer>
  );
};
