import React, { useState, useContext } from 'react';
import './App.css';
import { ThemeProvider } from 'styled-components';
import Navbar from './Components/Navbar';
import Home from './Components/Home';
import About from './Components/About';
import Work from './Components/Work';
import Contact from './Components/Contact';
import { lightTheme, darkTheme } from './styles/theme';
import { AppContainer, ContentContainer } from './styles/styles';
import { store } from './store';
import { AnimatePresence } from 'framer-motion';

function App() {
  const [theme, setTheme] = useState('light');
  const globalState = useContext(store);
  const {
    state: { location }
  } = globalState;

  console.log(globalState);

  const getTheme = () => (theme === 'light' ? lightTheme : darkTheme);

  return (
    <ThemeProvider theme={getTheme()}>
      <AppContainer>
        <Navbar />
        <ContentContainer>
          <AnimatePresence exitBeforeEnter>
            {location === 0 && <Home key='home' />}
            {location === 1 && <About key='about' />}
            {location === 2 && <Work key='work' />}
            {location === 3 && <Contact key='contact' />}
          </AnimatePresence>
        </ContentContainer>
      </AppContainer>
    </ThemeProvider>
  );
}

export default App;
