import React from 'react';
import { Container } from './styles/styles.js';

export const Layout = ({ children, ...props }) => {
  return (
    <Container
      initial={{ opacity: 0, y: 300 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -300 }}
      transition={{ duration: 0.2 }}
    >
      {children}
    </Container>
  );
};
